import React, { useState, useEffect } from 'react'
import MoviePoster from './MoviePoster'

function Category() { 

    const [state, setState] = useState({movies:[]});

    const getJSONdata = (data, multiPageArray) => {
    
        //console.log("AllData:" + JSON.stringify(data));
        var moviepage = data.data.results.map(movie =>          
            {
              //console.log("Movie ID:" + (movie.id));
              return {
                    id: movie.id,
                    title: movie.title,
                    posterPath: movie.poster_path,
                    plot: movie.overview,
                    userRating: movie.vote_average,
                    releaseDate: movie.release_date
                }
            }      
        );
        setState({ movies: [...moviepage]});
        //console.log("AllData:" + JSON.stringify(moviepage));            
    }

    const getMovies = () => {
        fetch('http://api.themoviedb.org/3/movie/popular?api_key=3d2560b40244f01afa57b67b4f3390a1')
              .then(response => response.json())
              .then(data=>getJSONdata({data},[]));
      }
    
    useEffect(() => {
      getMovies();
     },{});

    return (
        <div className="Sectionlimit">            
                <div className="Section1"></div>
                <div className="Section2"></div>
                <div className="Section3">
                    <div className="Header1">Pop Movies</div>
                </div>
                <div className="Section4"></div>
                <div className="Section5"><img className="Image1" src="tripledots.png"/> </div>
                <div className="Section6"></div>      
                <div className="Section7"> </div>
                    {state.movies.map((movie, index) =>
                            <MoviePoster
                            id={movie.id}
                            index={index}
                            image={"http:\/\/image.tmdb.org\/t\/p\/w185" + movie.posterPath}
                            title={movie.title}
                            />
                        )}
               
        </div>
    );
}

export default Category;
//{(state.movies.length==0) ? " No movies found. " : " "} 