import React, { useState, useEffect } from 'react'
import { Link } from "react-router-dom";

function Details({match}) {

    const [state, setState] = useState({movie:{}});
    const [video1, setVideo1] = useState('');
    const [video2, setVideo2] = useState('');
    const [favorite, setFavorite] = useState(0);

    const getJSONdata = (data, multiPageArray) => {
    
        console.log("AllData:" + JSON.stringify(data));
        console.log("Movie ID:" + (data.data.title));

        var str = data.data.release_date;
        str = str.slice(0,4); //Get year

        setState({ movie: JSON.parse(JSON.stringify({
                id: data.data.id,
                title: data.data.title,
                posterPath: data.data.poster_path,
                plot: data.data.overview,
                userRating: data.data.vote_average,
                minutes: data.data.runtime,
                releaseDate: str
            }))});        

        console.log("AllData:" + JSON.stringify(state.movie));            
    }

    const getJSONdataVideos = (data2, multiPageArray) => {
    
        console.log("AllData:" + JSON.stringify(data2));
        //console.log("Movie ID:" + (data.data.title));

        var moviepage = data2.data.results.map(video =>          
            {
              //console.log("Movie ID:" + (movie.id));
              return {
                    key: video.key
                }
            }      
        );

        console.log("AllMovieVideos:" + JSON.stringify(moviepage[0].key));
        console.log("AllMovieVideos:" + JSON.stringify(moviepage[1].key));


        setVideo1(moviepage[0].key);
        setVideo2(moviepage[1].key);
        /*setVideos({ video: JSON.parse(JSON.stringify({
                video1: moviepage[0].key,
                video2: moviepage[1].key                
            }))});
            */
        //{...movie, video1: moviepage[0].key, video2: moviepage[1].key});
        //console.log("AllMovieVideos:" + fruit);            
        
    }

    const getMovies = () => {
        fetch('http://api.themoviedb.org/3/movie/' + match.params.movieID + '?api_key=3d2560b40244f01afa57b67b4f3390a1')
              .then(response => response.json())
              .then(data=>getJSONdata({data},[]));
      }
    
      const getVideos = () => {
        fetch('http://api.themoviedb.org/3/movie/' + match.params.movieID + '/videos?api_key=3d2560b40244f01afa57b67b4f3390a1')
              .then(response => response.json())
              .then(data=>getJSONdataVideos({data},[]));
      }

    useEffect(() => {
      getMovies();
      getVideos();
     },{});

    return (
        <div className="Sectionlimit">            
                <div className="Section1"></div>
                <div className="Section2"></div>
                <div className="Section3">
                    <div className="Header1"><Link  className="Link1" to={"/"}><img src="../arrow.png"/>&nbsp;MovieDetails</Link></div>
                </div>
                <div className="Section4"></div>
                <div className="Section5"><img className="Image1" src="../tripledots.png"/></div>
                <div className="Section6"></div>      
                <div className="Section7"></div>
                <div className="Section9"></div>
                <div className="Section10"></div>
                <div className="Section11"><div className="Header2">{!(state.movie) ? " No movies found. " : state.movie.title }</div></div>
                <div className="Section12"></div>
                <div className="Section13"></div>

                <div className="Section14"></div>
                <div className="Section15"></div>
                <div className="Section16"><img className="Image3" src={"http:\/\/image.tmdb.org\/t\/p\/w154" + state.movie.posterPath} alt={state.movie.title} /></div>
                <div className="Section17"></div>
                <div className="Section18">
                    <div className="Section20"><div className="Header3">{state.movie.releaseDate}</div></div>    
                    <div className="Section21"></div>
                    <div className="Section22"><div className="Header4">{state.movie.minutes} mins</div></div>
                    <div className="Section23"></div>
                    <div className="Section24"><div className="Header5">{state.movie.userRating}/10</div></div>
                    <div className="Section25"></div>
                    <div className="Section26"><div className="Rectangle"><div className="Header6">MARK AS<br/>FAVORITE</div></div></div>
                    <div className="Section27"></div>
                </div>
                <div className="Section19"></div>
                <div className="Section28"></div>
               
                
                    <div className="Section30"><div className="Header7">{state.movie.plot}</div></div>
                   

                <div className="Section32"></div>

                <div className="Section33"></div>
                <div className="Section34"></div>
                <div className="Section35"></div>

                <div className="Section36"></div>
                
                <div className="Section37"></div>
                <div className="Section38"><div className="Header8">Trailers:</div></div>
                <div className="Section39"></div>

                <div className="Section40"></div>

                <div className="Section41"></div>
                <div className="Section42"><a href={'https://www.youtube.com/watch?v='+video1}><img className="Image4" src="../playbutton.png"/></a></div>
                <div className="Section43"></div>
                <div className="Section44"><div className="Header9">Trailer 1</div></div>
                <div className="Section45"></div>

                <div className="Section46"></div>

                <div className="Section33"></div>
                <div className="Section34"></div>
                <div className="Section35"></div>

                <div className="Section47"></div>

                <div className="Section41"></div>
                <div className="Section42"><a href={'https://www.youtube.com/watch?v='+video2}><img className="Image4" src="../playbutton.png"/></a></div>
                <div className="Section43"></div>
                <div className="Section44"><div className="Header9">Trailer 2</div></div>
                <div className="Section45"></div>

                <div className="Section48"></div>
        </div>
    );
}



export default Details;